.category {
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 10px;
}

.category h1 {
    color: #79bf99;
    font-size: 2em;
    margin-bottom: 20px;
}

.category h2 {
    color: #333;
    margin: 20px 0;
}

.category a {
    display: block;
    color: #333;
    background-color: #fff;
    text-decoration: none;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    transition: all 0.3s ease;

    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.category a:hover {
    color: #79bf99;
    transform: scale(1.02);
}
