.home {
    max-width: 1200px;
    margin: 30px auto;
    padding: 20px;
    background-color: rgb(187, 216, 187);
    border-radius: 15px;

  }
  
  .home h2 {
    margin-bottom: 30px;
  }
  
  .home .login-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .home .login-form input,
  .home .login-form button {
    padding: 15px;
    font-size: 16px;
    border-radius: 15px;
  }
  
  .home .login-form button {
    background-color: #e8e58e;
    color: rgb(34, 5, 5);
    border: none;
    border-radius: 15px;
    cursor: pointer;
  }
  .weather-box {
    padding: 20px;
    margin-top: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #f8f8f8;
    background-size: cover;
  }
  
  .weather-box h2 {
    margin-bottom: 10px;
  }
  