.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 125vh;
  border-radius: 15px;
  background-color: #f5f5f5; /* 밝은 배경색으로 변경 */
  font-family: Arial, sans-serif;
  padding: 20px;
}

.searchBar {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.searchInput {
  width: 300px;
  height: 30px;
  margin-right: 10px;
  border-radius: 15px;
  border: none; /* 테두리 제거 */
  padding: 10px; /* 입력 편의성을 위한 패딩 추가 */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* 그림자 효과 추가 */
}

.searchButton {
  height: 35px;
  background-color: #79bf99; /* 주황색에서 초록색으로 변경 */
  border-radius: 15px;
  font-weight: bold;
  color: #fff; /* 글자색을 흰색으로 변경 */
  font-size: 15px;
  padding: 10px 20px;
  border: none; /* 테두리 제거 */
  cursor: pointer; /* 마우스 오버시 손가락 모양으로 변경 */
}

.searchRank {
  margin-top: 5px;
  margin-bottom: 10px;
}

.profileCard {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px; /* 카드 간격 추가 */
}
