.review-container {
  width: 80%;
  margin: 0 auto;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 10px;
  font-family: Arial, sans-serif;
}

.review-input {
  width: 100%;
  height: 50px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 10px;
}

.review-button {
  width: 100%;
  padding: 10px;
  background-color: #79bf99;
  color: #fff;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.review-button:hover {
  background-color: #669f80;
}

.login-alert {
  color: red;
  margin-bottom: 10px;
}

.review-list {
  list-style: none;
  padding-left: 0;
  margin-top: 20px;
}

.review-item {
  padding: 10px;
  background-color: #fff;
  margin-bottom: 10px;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.review-username {
  font-weight: bold;
  color: #79bf99;
}
