.apply {
    width: 100%;
    background-color: #79bf99;
    color: white;
    padding: 20px 0;
    border-radius: 15px;
  }
  
  .apply nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .apply .logo {
    font-size: 25px;
    font-weight: bold;
    color: white;
    text-decoration: none;
  }
  
  .apply .nav-links {
    display: flex;
    gap: 20px;
  }
  
  .apply .nav-links a {
    color: white;
    text-decoration: none;
  }
  